<template>
	<div
		:style='{"width":"100%","padding":"30px 7% 40px","margin":"0px auto","position":"relative","background":"#fff"}'>
		<el-form
			:style='{"border":"0px solid #eee","padding":"30px 0","alignItems":"flex-start","flexWrap":"wrap","background":"#fff","display":"flex","width":"100%","position":"relative"}'
			class="add-update-preview" ref="ruleForm" :model="ruleForm" :rules="rules" label-width="120px">
			<el-form-item
				:style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}'
				label="" prop="chuchangbianhao">
				<el-input v-model="ruleForm.chuchangbianhao" placeholder="" disabled></el-input>
			</el-form-item>
			<el-form-item
				:style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}'
				label="" prop="cheweibianhao">
				<el-input v-model="ruleForm.cheweibianhao" placeholder="" clearable
					:disabled=" false  ||ro.cheweibianhao"></el-input>
			</el-form-item>
			<el-form-item
				:style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}'
				label="" prop="cheweiweizhi">
				<el-input v-model="ruleForm.cheweiweizhi" placeholder="" clearable
					:disabled=" false  ||ro.cheweiweizhi"></el-input>
			</el-form-item>
			<el-form-item
				:style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}'
				label="" v-if="type!='cross' || (type=='cross' && !ro.cheweitupian)" prop="cheweitupian">
				<file-upload tip="" action="file/upload" :limit="3" :multiple="true"
					:fileUrls="ruleForm.cheweitupian?ruleForm.cheweitupian:''"
					@change="cheweitupianUploadChange"></file-upload>
			</el-form-item>
			<el-form-item
				:style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}'
				class="upload" v-else label="" prop="cheweitupian">
				<img v-if="ruleForm.cheweitupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;"
					v-bind:key="index" :src="ruleForm.cheweitupian.split(',')[0]" width="100" height="100">
				<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index"
					v-for="(item,index) in ruleForm.cheweitupian.split(',')" :src="baseUrl+item" width="100"
					height="100">
			</el-form-item>
			<el-form-item
				:style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}'
				label="" prop="meixiaoshijia">
				<el-input-number v-model="ruleForm.meixiaoshijia" placeholder=""
					:disabled="ro.meixiaoshijia"></el-input-number>
			</el-form-item>
			<el-form-item
				:style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}'
				label="" prop="chepaihao">
				<el-input v-model="ruleForm.chepaihao" placeholder="" clearable
					:disabled=" false  ||ro.chepaihao"></el-input>
			</el-form-item>
			<el-form-item
				:style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}'
				label="" prop="chezhuhao">
				<el-input v-model="ruleForm.chezhuhao" placeholder="" clearable
					:disabled=" false  ||ro.chezhuhao"></el-input>
			</el-form-item>
			<el-form-item
				:style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}'
				label="" prop="chezhuxingming">
				<el-input v-model="ruleForm.chezhuxingming" placeholder="" clearable
					:disabled=" false  ||ro.chezhuxingming"></el-input>
			</el-form-item>
			<el-form-item
				:style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}'
				label="" prop="shoujihaoma">
				<el-input v-model="ruleForm.shoujihaoma" placeholder="" clearable
					:disabled=" false  ||ro.shoujihaoma"></el-input>
			</el-form-item>
			<el-form-item
				:style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}'
				label="" prop="ruchangshijian">
				<el-date-picker :disabled="true  ||ro.ruchangshijian" value-format="yyyy-MM-dd HH:mm:ss"
					v-model="ruleForm.ruchangshijian" type="datetime" placeholder="">
				</el-date-picker>
			</el-form-item>
			<el-form-item
				:style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}'
				label="" prop="chuchangshijian">
				<el-date-picker :disabled="true  ||ro.chuchangshijian" value-format="yyyy-MM-dd HH:mm:ss"
					v-model="ruleForm.chuchangshijian" type="datetime" placeholder="">
				</el-date-picker>
			</el-form-item>
			<el-form-item
				:style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}'
				label="" prop="tingcheshizhang">
				<el-input v-model="tingcheshizhang" placeholder="" disabled></el-input>
			</el-form-item>
			<el-form-item
				:style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}'
				label="" prop="tingchefeiyong">
				<el-input v-model="tingchefeiyong" placeholder="" disabled></el-input>
			</el-form-item>

			<el-form-item :style='{"width":"100%","padding":"0","margin":"0","textAlign":"center"}'>
				<el-button
					:style='{"border":"0px solid #eccc19","cursor":"pointer","padding":"0","margin":"0 20px 0 0","color":"#fff","borderRadius":"20px","background":"#0062a5","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}'
					type="primary" @click="onSubmit"></el-button>
				<el-button
					:style='{"border":"1px solid #ccc","cursor":"pointer","padding":"0","margin":"0","outline":"none","color":"#666","borderRadius":"20px","background":"none","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}'
					@click="back()"></el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			let self = this
			return {
				id: '',
				baseUrl: '',
				ro: {
					chuchangbianhao: false,
					cheweibianhao: false,
					cheweiweizhi: false,
					cheweitupian: false,
					meixiaoshijia: false,
					chepaihao: false,
					chezhuhao: false,
					chezhuxingming: false,
					shoujihaoma: false,
					ruchangshijian: false,
					chuchangshijian: false,
					tingcheshizhang: false,
					tingchefeiyong: false,
					crossuserid: false,
					crossrefid: false,
					ispay: false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					chuchangbianhao: this.getUUID(),
					cheweibianhao: '',
					cheweiweizhi: '',
					cheweitupian: '',
					meixiaoshijia: '',
					chepaihao: '',
					chezhuhao: '',
					chezhuxingming: '',
					shoujihaoma: '',
					ruchangshijian: '',
					chuchangshijian: '',
					tingcheshizhang: '',
					tingchefeiyong: '',
					crossuserid: '',
					crossrefid: '',
					ispay: '',
				},


				rules: {
					chuchangbianhao: [],
					cheweibianhao: [],
					cheweiweizhi: [],
					cheweitupian: [],
					meixiaoshijia: [{
						validator: this.$validate.isNumber,
						trigger: 'blur'
					}, ],
					chepaihao: [],
					chezhuhao: [],
					chezhuxingming: [],
					shoujihaoma: [],
					ruchangshijian: [],
					chuchangshijian: [],
					tingcheshizhang: [{
						validator: this.$validate.isNumber,
						trigger: 'blur'
					}, ],
					tingchefeiyong: [{
						validator: this.$validate.isNumber,
						trigger: 'blur'
					}, ],
					crossuserid: [],
					crossrefid: [],
					ispay: [],
				},
				centerType: false,
			};
		},
		computed: {
			tingcheshizhang: {
				get: function() {
					let h = this.ruleForm
					let a = 'h.ruchangshijian-h.chuchangshijian'
					let n = a.split('-')
					let hour = this.getHour(h[n[0].split('h.')[1]], h[n[1].split('h.')[1]])
					this.ruleForm.tingcheshizhang = hour ? hour : 0
					return hour ? hour : 0
				}

			},
			tingchefeiyong: {
				get: function() {
					let c = this.ruleForm
					let a = c.meixiaoshijia * c.tingcheshizhang
					this.ruleForm.tingchefeiyong = a ? Number(a).toFixed(2) : 0
					return a ? Number(a).toFixed(2) : 0
				}

			},



		},
		components: {},
		created() {
			if (this.$route.query.centerType) {
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
			this.ruleForm.chuchangshijian = this.getCurDateTime()
		},
		methods: {
			//  
			getHour(first, last) {
				let date1 = new Date(first)
				let date2 = new Date(last)
				let a = date1.getTime();
				let b = date2.getTime();
				var count = 0;
				for (var i = a; i < b; i += 3600 * 1000) {
					count++;
				}
				return count;
			},
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 
			download(file) {
				window.open(`${file}`)
			},
			// 
			init(type) {
				this.type = type;
				if (type == 'cross') {
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj) {
						if (o == 'chuchangbianhao') {
							this.ruleForm.chuchangbianhao = obj[o];
							this.ro.chuchangbianhao = true;
							continue;
						}
						if (o == 'cheweibianhao') {
							this.ruleForm.cheweibianhao = obj[o];
							this.ro.cheweibianhao = true;
							continue;
						}
						if (o == 'cheweiweizhi') {
							this.ruleForm.cheweiweizhi = obj[o];
							this.ro.cheweiweizhi = true;
							continue;
						}
						if (o == 'cheweitupian') {
							this.ruleForm.cheweitupian = obj[o].split(",")[0];
							this.ro.cheweitupian = true;
							continue;
						}
						if (o == 'meixiaoshijia') {
							this.ruleForm.meixiaoshijia = obj[o];
							this.ro.meixiaoshijia = true;
							continue;
						}
						if (o == 'chepaihao') {
							this.ruleForm.chepaihao = obj[o];
							this.ro.chepaihao = true;
							continue;
						}
						if (o == 'chezhuhao') {
							this.ruleForm.chezhuhao = obj[o];
							this.ro.chezhuhao = true;
							continue;
						}
						if (o == 'chezhuxingming') {
							this.ruleForm.chezhuxingming = obj[o];
							this.ro.chezhuxingming = true;
							continue;
						}
						if (o == 'shoujihaoma') {
							this.ruleForm.shoujihaoma = obj[o];
							this.ro.shoujihaoma = true;
							continue;
						}
						if (o == 'ruchangshijian') {
							this.ruleForm.ruchangshijian = obj[o];
							this.ro.ruchangshijian = true;
							continue;
						}
						if (o == 'chuchangshijian') {
							this.ruleForm.chuchangshijian = obj[o];
							this.ro.chuchangshijian = true;
							continue;
						}
						if (o == 'tingcheshizhang') {
							this.ruleForm.tingcheshizhang = obj[o];
							this.ro.tingcheshizhang = true;
							continue;
						}
						if (o == 'tingchefeiyong') {
							this.ruleForm.tingchefeiyong = obj[o];
							this.ro.tingchefeiyong = true;
							continue;
						}
						if (o == 'crossuserid') {
							this.ruleForm.crossuserid = obj[o];
							this.ro.crossuserid = true;
							continue;
						}
						if (o == 'crossrefid') {
							this.ruleForm.crossrefid = obj[o];
							this.ro.crossrefid = true;
							continue;
						}
					}
				} else if (type == 'edit') {
					this.info()
				}
				// 
				this.$http.get(this.userTableName + '/session', {
					emulateJSON: true
				}).then(res => {
					if (res.data.code == 0) {
						var json = res.data.data;
					}
				});

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},

			// 
			// 
			info() {
				this.$http.get(`cheliangchuchang/detail/${this.$route.query.id}`, {
					emulateJSON: true
				}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 
			onSubmit() {
				if (this.ruleForm.tingcheshizhang == 0) {
					this.$message.error('')
					return false
				}
				if (this.ruleForm.tingchefeiyong == 0) {
					this.$message.error('')
					return false
				}
				if (this.ruleForm.chuchangbianhao) {
					this.ruleForm.chuchangbianhao = String(this.ruleForm.chuchangbianhao)
				}
				//
				var crossuserid;
				var crossrefid;
				var crossoptnum;
				this.$refs["ruleForm"].validate(valid => {
					if (valid) {
						if (this.type == 'cross') {
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if (statusColumnName && statusColumnName != '') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if (!statusColumnName.startsWith("[")) {
									for (var o in obj) {
										if (o == statusColumnName) {
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									this.$http.post(table + '/update', obj).then(res => {});
								} else {
									crossuserid = Number(localStorage.getItem('frontUserid'));
									crossrefid = obj['id'];
									crossoptnum = localStorage.getItem('statusColumnName');
									crossoptnum = crossoptnum.replace(/\[/, "").replace(/\]/, "");
								}
							}
						}
						if (crossrefid && crossuserid) {
							this.ruleForm.crossuserid = crossuserid;
							this.ruleForm.crossrefid = crossrefid;
							var params = {
								page: 1,
								limit: 10,
								crossuserid: crossuserid,
								crossrefid: crossrefid,
							}
							this.$http.get('cheliangchuchang/list', {
								params: params
							}).then(res => {
								if (res.data.data.total >= crossoptnum) {
									this.$message({
										message: localStorage.getItem('tips'),
										type: 'error',
										duration: 1500,
									});
									return false;
								} else {
									// 

									
									this.$http.post(
										`cheliangchuchang/${this.ruleForm.id?'update':this.centerType?'save':'add'}`,
										this.ruleForm).then(res => {
										if (res.data.code == 0) {
											this.$http.get('cheweixinxi/query',{
												params: {
													cheweibianhao: this.ruleForm.cheweibianhao
												}
											}).then(rs1=>{
												if(rs1.data&&rs1.data.code==0){
													rs1.data.data.cheweizhuangtai = ''
													this.$http.post('cheweixinxi/update',rs1.data.data).then(()=>{
														this.$message({
															message: '',
															type: 'success',
															duration: 1500,
															onClose: () => {
																this.$router.go(-1);
															}
														});
													})
												}
											})
											
										} else {
											this.$message({
												message: res.data.msg,
												type: 'error',
												duration: 1500
											});
										}
									});
								}
							});
						} else {


							this.$http.post(
								`cheliangchuchang/${this.ruleForm.id?'update':this.centerType?'save':'add'}`,
								this.ruleForm).then(res => {
								if (res.data.code == 0) {
									this.$message({
										message: '',
										type: 'success',
										duration: 1500,
										onClose: () => {
											this.$router.go(-1);
										}
									});
								} else {
									this.$message({
										message: res.data.msg,
										type: 'error',
										duration: 1500
									});
								}
							});
						}
					}
				});
			},
			// uuid
			getUUID() {
				return new Date().getTime();
			},
			// 
			back() {
				this.$router.go(-1);
			},
			cheweitupianUploadChange(fileUrls) {
				this.ruleForm.cheweitupian = fileUrls.replace(new RegExp(this.$config.baseUrl, "g"), "");
			},
		}
	};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
